@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700&display=swap);

.form-container{
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 9.5px 19px rgba(0, 0, 0, 0.1), 0 7.5px 6px rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  padding: 2rem;
}

.form {
  width: 60%;
}

.input-item{
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
}


@media screen and (max-width: 992px) {
  .form-container {
    width: 30rem;
  }
}

@media screen and (max-width: 700px) {
  .form-container {
    width: 20rem;
  }

  .form{
    width: 100%;
  }
}

@media screen and (max-width: 400px) {
  .form {
    display: none;
  }

  .unsupported-device {
    display: block;
  }
  
  /* .form-container {
    width: 15rem;
  }

  .login-button{
    transform: translateX(-12.5%);
  } */
}

@media screen and (max-width: 280px) {
  .form-container {
    display: none;
  }
}


@media only screen and (max-width: 600px) {

}

.navbar {
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  background-color: #1890FF;
  margin-bottom: 2rem;
}

.navbar p{
  color: white;
  text-decoration: none;
  cursor: pointer;
}


ul {
  list-style: none;
}

.navbar-content {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-style: normal;
}

.navbar-title{
  font-size: 1.2rem;
  -webkit-transform: translateY(1rem);
          transform: translateY(1rem);
}

.ul-links {
  display: flex;
  font-size: 1rem;
  -webkit-transform: translateY(.9rem);
          transform: translateY(.9rem);
}

.ul-links li {
  margin-left: 2rem;
}

@media only screen and (max-width: 700px) {
  .navbar-title{
    font-size: 1rem;
    -webkit-transform: translateY(1rem);
            transform: translateY(1rem);
  }
  
  .ul-links {
    display: flex;
    font-size: .8rem;
    -webkit-transform: translateY(.9rem);
            transform: translateY(.9rem);
  }
}


.dashboard-select-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 3rem auto;
}

.dashboard-content-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100vw;
    /* background-color: red; */
}

.dashboard-content-wrapper h4{
    margin-bottom: 1rem;
}

.quest-item, .achievement-item, .user-item, .rank-item {
    box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: wrap;
    padding: 2rem;
    border-radius: 10px;
    margin: 2rem auto;
}

.quest-item > div, .achievement-item > div, .user-item > div, .rank-item > div {
    margin: auto 2rem;
}

.create-rank-container > input {
    margin: .5rem auto;
}

.ant-delete-button{
    margin-right: 2rem;
}

/* global styling*/

* {
    padding: 0;
    margin: 0;
  /* font-family: "Montserrat Alternates", sans-serif; */
  font-family: 'Raleway', sans-serif;
  text-decoration: none;
}

body{
  overflow-x: hidden;
}

.spinner{
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

